import React from 'react'
import PropTypes from 'prop-types'
import { graphql } from 'gatsby'
import { navigate } from 'gatsby'
import withStyles from '@material-ui/core/styles/withStyles'
import Layout from '../components/layout/layout'
import SEO from '../components/seo/seo'
import theme from '../lib/theme'
import SectionHeader from '../components/sections-header/section-header'
import Paper from '@material-ui/core/Paper'
import { AuthorById } from '../lib/author-by-id'
import CardHeader from '@material-ui/core/CardHeader'
import Button from '@material-ui/core/Button'
import Link from '@material-ui/core/Link'

const styles = withStyles(() => ({
  root: {
    textAlign: 'left',
    margin: '240px 120px',
    [theme.breakpoints.down('sm')]: {
      margin: '142px 60px',
    },
    [theme.breakpoints.down('xs')]: {
      margin: '37px 10px',
    },
  },
  content: {
    padding: theme.spacing(6),
    fontSize: theme.spacing(2),
    lineHeight: theme.spacing(0.21),
    [theme.breakpoints.down('sm')]: {
      padding: theme.spacing(3),
      fontSize: theme.spacing(2.25),
    },
    [theme.breakpoints.down('xs')]: {
      padding: theme.spacing(2),
      fontSize: theme.spacing(2.5),
    },
  },
  meta: {
    justifyContent: 'right',
  },
  offset: {
    display: 'block',
    position: 'relative',
    visibility: 'hidden',
    [theme.breakpoints.down('xl')]: {},
    [theme.breakpoints.down('lg')]: {
      top: '-115px',
    },
    [theme.breakpoints.down('md')]: {
      top: '-80px',
    },
  },
  button: {
    width: '100%',
    textAlign: 'center',
    margin: theme.spacing(4, 0, 2, 0),
  },
  buttonText: {
    color: theme.palette.common.white,
    [theme.breakpoints.down('xs')]: {
      width: '100%',
    },
  },
}))

const BlogEntryAuthor = ({ name, link }) => (
  <a href={link} target="_blank" rel="noreferrer">
    {name}
  </a>
)

const BlogEntry = ({ classes, data }) => {
  const { wordpressPost } = data
  const title = wordpressPost['title']

  const author = AuthorById(wordpressPost['author'])

  return (
    <Layout>
      <SEO title={title} />
      <span id="top" className={classes.offset} />
      <div className={classes.root}>
        <Paper elevation={3}>
          <div className={classes.content}>
            <SectionHeader title={title} align={'left'} />
            <div align={'left'}>
              <CardHeader
                title={
                  <BlogEntryAuthor name={author.name} link={author.link} />
                }
                subheader={wordpressPost['date']}
              />
            </div>
            <div
              className={classes.content}
              dangerouslySetInnerHTML={{ __html: wordpressPost['content'] }}
            />
            <div className={classes.button}>
              <Button
                variant={'contained'}
                size={'large'}
                color={'primary'}
                className={classes.buttonText}
                href={'mailto:hello@compose.us?subject=Servus'}
              >
                Ich möchte mehr wissen!
              </Button>
            </div>
          </div>
        </Paper>
      </div>
    </Layout>
  )
}

export const query = graphql`
  query($slug: String!) {
    wordpressPost(slug: { eq: $slug }) {
      id
      date(formatString: "MM/DD/YYYY")
      link
      title
      path
      slug
      author
      content
      categories {
        name
        slug
      }
    }
  }
`

BlogEntry.propTypes = {
  classes: PropTypes.object.isRequired,
  data: PropTypes.object.isRequired,
}

export default styles(BlogEntry)
